.desktop_wordcard{
    /* width: 33%; */
    flex: 1 0 calc(33% - 2px*2);
    margin: 3px 2px;
}

.tablet_wordcard{
    /* width: 50%; */
    flex: 1 0 calc(50% - 2px*2);
    margin: 3px 2px;
}

.mobile_wordcard{
    width: 100%;
    margin: 3px 2px;
}